import * as React from 'react';
import flatMap from 'array.prototype.flatmap';
import Linkify from 'react-linkify';
import {
    FormattedMessage,
    injectIntl,
    IntlShape,
    WrappedComponentProps,
} from 'react-intl';
import {GedcomData, pointerToId} from './util/gedcom_util';
import {GedcomEntry} from 'parse-gedcom';
import {translateDate} from './util/date_util';
import {Button} from "semantic-ui-react";

interface Props {
    gedcom: GedcomData;
    indi: string;
}

const EVENT_TAGS = ['BIRT', 'BAPM', 'CHR', 'EVEN', 'CENS', 'DEAT', 'BURI'];
const EXCLUDED_TAGS = ['NAME', 'SEX', 'FAMC', 'FAMS', 'NOTE'];
const TAG_DESCRIPTIONS = new Map([
    ['ADOP', 'Adoption'],
    ['BAPM', 'Baptism'],
    ['BIRT', 'Birth'],
    ['BURI', 'Burial'],
    ['CENS', 'Census'],
    ['CHR', 'Christening'],
    ['CREM', 'Cremation'],
    ['DEAT', 'Death'],
    ['EDUC', 'Education'],
    ['EMAIL', 'E-mail'],
    ['EMIG', 'Emigration'],
    ['EVEN', 'Event'],
    ['FACT', 'Fact'],
    ['IMMI', 'Immigration'],
    ['MARR', 'Marriage'],
    ['MILT', 'Military services'],
    ['NATU', 'Naturalization'],
    ['OCCU', 'Occupation'],
    ['TITL', 'Title'],
    ['WWW', 'WWW'],
]);

function translateTag(tag: string) {
    const normalizedTag = tag.replace(/_/g, '');
    return (
        <FormattedMessage
            id={`gedcom.${normalizedTag}`}
            defaultMessage={TAG_DESCRIPTIONS.get(normalizedTag) || normalizedTag}
        />
    );
}

function joinLines(lines: (JSX.Element | string)[]) {
    return (
        <>
            {lines.map((line, index) => (
                <div key={index}>
                    <Linkify properties={{target: '_blank'}}>{line}</Linkify>
                    <br/>
                </div>
            ))}
        </>
    );
}

/**
 * Returns the data for the given GEDCOM entry as an array of lines. Supports
 * continuations with CONT and CONC.
 */
function getData(entry: GedcomEntry) {
    const result = [entry.data];
    entry.tree.forEach((subentry) => {
        if (subentry.tag === 'CONC' && subentry.data) {
            const last = result.length - 1;
            result[last] += subentry.data;
        } else if (subentry.tag === 'CONT' && subentry.data) {
            result.push(subentry.data);
        }
    });
    return result;
}

function eventDetails(entry: GedcomEntry, intl: IntlShape, gedcom: GedcomData) {
    const lines = [];
    if (entry.data && entry.data.length > 1) {
        lines.push(<i>{entry.data}</i>);
    }
    entry.tree
        .filter((subentry) => subentry.tag === 'NOTE')
        .forEach((note) =>
            getData(note).forEach((line) => lines.push(<i>{line}</i>)),
        );
    return (
        <div className="content">
            {eventHeader(entry, intl)}
            {eventPlace(entry)}
            <span>{joinLines(lines)}</span>
            {eventSource(entry, gedcom)}
        </div>
    );
}

function eventHeader(entry: GedcomEntry, intl: IntlShape) {
    const date = entry.tree.find((subEntry) => subEntry.tag === 'DATE');
    if (date && date.data) {

        return (
            <div>
                <span className="ui header">{translateTag(entry.tag)}</span>
                <span className="ui subheader right floated">{translateDate(date.data, intl)}</span>
            </div>
        );
    }
}

function eventPlace(entry: GedcomEntry) {
    const place = entry.tree.find((subEntry) => subEntry.tag === 'PLAC');
    if (place && place.data) {
        return (
            <div className="description">{getData(place)}</div>
        );
    }
}

function eventSource(entry: GedcomEntry, gedcom: GedcomData) {
    const sources: JSX.Element[] = []
    entry.tree
        .filter((subEntry) => subEntry.tag === 'SOUR')
        .forEach((source) => sources.push(sourceDetails(source, gedcom)));

    if (sources && sources.length) {
        return (
            <span className="extra">
           rdo: {sources.map((source, index) => (
                <span key={index}>
                {source}
            </span>
            ))}
        </span>
        );
    }
    return null;
}

function dataDetails(entry: GedcomEntry) {
    const lines = [];
    if (entry.data) {
        lines.push(...getData(entry));
    }
    entry.tree
        .filter((subentry) => subentry.tag === 'NOTE')
        .forEach((note) =>
            getData(note).forEach((line) => lines.push(<i>{line}</i>)),
        );
    if (!lines.length) {
        return null;
    }
    return (
        <>
            <div className="ui sub header">{translateTag(entry.tag)}</div>
            <span>{joinLines(lines)}</span>
        </>
    );
}

function sourceDetails(sourceReference: GedcomEntry, gedcom: GedcomData) {
    const source = dereference(sourceReference, gedcom);
    const sourceTitle = source.tree.find((subEntry) => subEntry.tag === 'TITL')

    if (sourceTitle && sourceTitle.data) {
        const sourcePage = sourceReference.tree.find((subEntry) => subEntry.tag === 'PAGE');
        if (sourcePage && sourcePage.data) {
            if (sourcePage.data.startsWith("http")) {
                return <Button className="mini ui button" as='a' target="_blank" rel="noreferrer"
                               href={sourcePage.data}>
                    {sourceTitle.data}
                </Button>;
            }
            return <>{sourceTitle.data} ({sourcePage.data})</>;
        }
        return <>{sourceTitle.data}</>;
    }
    return <></>
}

function noteDetails(entry: GedcomEntry) {
    return joinLines(
        getData(entry).map((line, index) => <i key={index}>{line}</i>),
    );
}

function nameDetails(entry: GedcomEntry) {
    return (
        <h2 className="ui header">
            {entry.data
                .split('/')
                .filter((name) => !!name)
                .map((name, index) => (
                    <div key={index}>
                        {name}
                        <br/>
                    </div>
                ))}
        </h2>
    );
}

function getDetails(
    entries: GedcomEntry[],
    tags: string[],
    detailsFunction: (entry: GedcomEntry) => JSX.Element | null,
): JSX.Element[] {
    return flatMap(tags, (tag) =>
        entries
            .filter((entry) => entry.tag === tag)
            .map((entry) => detailsFunction(entry)),
    )
        .filter((element) => element !== null)
        .map((element, index) => (
            <div className="ui segment" key={index}>
                {element}
            </div>
        ));
}

function getEventDetails(
    entries: GedcomEntry[],
    tags: string[],
    detailsFunction: (entry: GedcomEntry) => JSX.Element | null,
): JSX.Element | null {
    const events = flatMap(tags, (tag) =>
        entries
            .filter((entry) => entry.tag === tag)
            .map((entry) => detailsFunction(entry)),
        )
        .filter((element) => element !== null);
    if (events && events.length) {
        return (
            <div className="ui segment divided items">
                {events.map((element, index) => (
                    <div className="ui item" key={index}>
                        {element}
                    </div>
                ))}
            </div>)
    }
    return null;
}

/**
 * Returns true if there is displayable information in this entry.
 * Returns false if there is no data in this entry or this is only a reference
 * to another entry.
 */
function hasData(entry: GedcomEntry) {
    return entry.tree.length > 0 || (entry.data && !entry.data.startsWith('@'));
}

function getOtherDetails(entries: GedcomEntry[]) {
    return entries
        .filter(
            (entry) =>
                !EXCLUDED_TAGS.includes(entry.tag) && !EVENT_TAGS.includes(entry.tag),
        )
        .filter(hasData)
        .map((entry) => dataDetails(entry))
        .filter((element) => element !== null)
        .map((element, index) => (
            <div className="ui segment" key={index}>
                {element}
            </div>
        ));
}

/**
 * If the entry is a reference to a top-level entry, the referenced entry is
 * returned. Otherwise, returns the given entry unmodified.
 */
function dereference(entry: GedcomEntry, gedcom: GedcomData) {
    if (entry.data) {
        const dereferenced = gedcom.other[pointerToId(entry.data)];
        if (dereferenced) {
            return dereferenced;
        }
    }
    return entry;
}

class DetailsComponent extends React.Component<Props & WrappedComponentProps,
    {}> {
    render() {
        const entries = this.props.gedcom.indis[this.props.indi].tree;
        const entriesWithData = entries
            .map((entry) => dereference(entry, this.props.gedcom))
            .filter(hasData);

        return (
            <div className="ui segments" id="details">
                {getDetails(entries, ['NAME'], nameDetails)}
                {getEventDetails(entries, EVENT_TAGS, (entry) =>
                    eventDetails(entry, this.props.intl, this.props.gedcom),
                )}
                {getOtherDetails(entriesWithData)}
                {getDetails(entriesWithData, ['NOTE'], noteDetails)}
            </div>
        );
    }
}

export const Details = injectIntl(DetailsComponent);
